// App.js
import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useLocation } from 'react-router-dom';
import { FiCalendar, FiGrid, FiPieChart, FiSettings, FiBell, FiMenu, FiX } from 'react-icons/fi';
import Dashboard from './pages/dashboard/Dashboard';
import CommunicationCalendar from './pages/calendar/CommunicationCalendar';
import CompanyManagement from './pages/admin/CompanyManagement';
import CommunicationSettings from './pages/admin/CommunicationSettings';
import Analytics from './pages/analytics/Analytics';
import Notifications from './pages/notifications/Notifications';
import { ThemeProvider } from './context/ThemeContext';
import ThemeSelector from './components/ThemeSelector';

function App() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const NavLink = ({ to, icon: Icon, text }) => {
    const location = useLocation();
    const isActive = location.pathname === to;
    
    return (
      <Link
        to={to}
        className={`flex items-center px-4 py-2 rounded-lg transition-colors duration-200 ${
          isActive 
            ? 'bg-blue-50 text-blue-600' 
            : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900'
        }`}
        onClick={() => setIsMobileMenuOpen(false)}
      >
        <Icon className="w-5 h-5 mr-3" />
        <span className="font-medium">{text}</span>
      </Link>
    );
  };

  return (
    <ThemeProvider>
      <div className="min-h-screen bg-gray-50">
        {/* Header */}
        <header className="bg-white shadow-sm fixed w-full top-0 z-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
              <div className="flex items-center">
                <Link to="/" className="flex items-center">
                  <span className="text-2xl font-bold text-blue-600">EntntTracker</span>
                </Link>
              </div>

              {/* Desktop Navigation */}
              <nav className="hidden md:flex items-center space-x-4">
                <NavLink to="/" icon={FiGrid} text="Dashboard" />
                <NavLink to="/calendar" icon={FiCalendar} text="Calendar" />
                <NavLink to="/analytics" icon={FiPieChart} text="Analytics" />
                <NavLink to="/settings" icon={FiSettings} text="Settings" />
                <button className="p-2 text-gray-400 hover:text-gray-500">
                  <FiBell className="w-6 h-6" />
                </button>
              </nav>

              {/* Mobile menu button */}
              <button
                className="md:hidden p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              >
                {isMobileMenuOpen ? (
                  <FiX className="w-6 h-6" />
                ) : (
                  <FiMenu className="w-6 h-6" />
                )}
              </button>
            </div>
          </div>

          {/* Mobile Navigation */}
          {isMobileMenuOpen && (
            <nav className="md:hidden bg-white border-t border-gray-200 p-4">
              <div className="flex flex-col space-y-2">
                <NavLink to="/" icon={FiGrid} text="Dashboard" />
                <NavLink to="/calendar" icon={FiCalendar} text="Calendar" />
                <NavLink to="/analytics" icon={FiPieChart} text="Analytics" />
                <NavLink to="/settings" icon={FiSettings} text="Settings" />
              </div>
            </nav>
          )}
        </header>

        {/* Main Content */}
        <main className="pt-16 pb-8">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
            <Routes>
              <Route path="/" element={<Dashboard />} />
              <Route path="/calendar" element={<CommunicationCalendar />} />
              <Route path="/companies" element={<CompanyManagement />} />
              <Route path="/settings" element={<CommunicationSettings />} />
              <Route path="/analytics" element={<Analytics />} />
              <Route path="/notifications" element={<Notifications />} />
            </Routes>
          </div>
        </main>

        {/* Footer */}
        <footer className="bg-white border-t border-gray-200">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
            <div className="flex justify-between items-center">
              <p className="text-gray-500 text-sm">
                 2024 EntntTracker. All rights reserved.
              </p>
              <div className="flex space-x-6">
                <a href="#" className="text-gray-400 hover:text-gray-500">
                  Privacy Policy
                </a>
                <a href="#" className="text-gray-400 hover:text-gray-500">
                  Terms of Service
                </a>
                <a href="#" className="text-gray-400 hover:text-gray-500">
                  Contact
                </a>
              </div>
            </div>
          </div>
        </footer>

        <ThemeSelector />
      </div>
    </ThemeProvider>
  );
}

export default App;
